.loading {
    border: 1px solid rgb(146, 146, 146);
    width: 180px;
    height: 14px;
    border-radius: 6px;
    -webkit-appearance: none;
}

.loading::-webkit-progress-bar {
    background-color: rgba(43, 42, 42, 0);
    border-radius: 6px;
}

.loadnum {
    position: relative;
    top: -16px;
    font-size: 14px;
}

.loading::-webkit-progress-value {
    opacity: 1;
    background-image: linear-gradient( 135deg, #A0FE65 10%, #FA016D 100%);
    border-radius: 6px;
}

.egg-container {
    display: flex;
    flex-direction: column;
    width: 300px;
    height: 300px;
    align-items: center;
    justify-content: center;
    user-select: none;
    background-image: linear-gradient( 135deg, #81FFEF 10%, #F067B4 100%);
    color: #b5f126;
    border-radius: 20px;
    box-shadow: 2px 2px 3px #7fe60a;
    margin: 100px;
    position: relative;
}

.eggimg {
    width: 200px;
    height: 200px;
}

.breakegg {
    position: relative;
    width: 200px;
    height: 200px;
}

.egg-head {
    animation: rotateegghead 0.5s;
    transform: rotateZ(-90deg) translateY(-50px);
}

.breakegg img {
    position: absolute;
    left: 0px;
}

.eggbtn {
    border: 0;
    margin: 10px;
    width: 100px;
    height: 20px;
    border-radius: 10px;
    color: #b5f126;
    background-color: rgba(43, 42, 42, 0.2);
    box-shadow: 2px 2px 3px #7fe60a;
}

.eggbtn:hover {
    background-color: rgba(107, 84, 84, 0.671);
}

.mon {
    display: flex;
    flex-direction: column;
    position: absolute;
    width: 300px;
    height: 300px;
    background-image: linear-gradient( 135deg, #81FFEF 10%, #F067B4 100%);
    align-items: center;
    justify-content: space-around;
    border-radius: 20px;
}

.monimg {
    width: 150px;
    height: 150px;
    animation: over 1s;
    transform: scale(1);
}

.nameinput {
    width: 80px;
    border-radius: 20px;
    text-align: center;
}

@keyframes rotateegghead {
    from {
        transform: rotateZ(0);
    }
    to {
        transform: rotateZ(-90deg) translateY(-50px);
    }
}

@keyframes over {
    from {
        transform: scale(0.2);
    }
    to {
        transform: scale(1);
    }
}